/** Media Queries **/
img,
picture {
  width: 100%;
  display: block;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

/** UI **/
* {
  margin: 0;
}

header {
  background-color: rgb(230, 227, 221);
  width: 100%;
  height: auto;
  display: flex;
  padding: 5px;
}

.container-logo {
  width: 40%;
  display: flex;
  align-items: center;
}

.logo {
  text-decoration: none;
  color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 5px;
}

.logo .nombre {
  font-family: "Montserrat", system-ui;
  font-optical-sizing: auto;
  font-size: 1.6rem;
  font-style: normal;
}

.logo .slogan {
  font-family: "Montserrat", system-ui;
  font-optical-sizing: auto;
  font-size: 0.7rem;
  font-style: normal;
}

.container-menu {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  width: 100%;
  align-items: center;
}

ul {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: flex-end;
}

ul li {
  list-style: none;
  margin: 0 10px;
}

ul li a {
  color: black;
  text-decoration: none;
}

ul li a p:hover {
  text-decoration: white underline 2px;
  transition: ease-out 0.2s;
  color: white;
  font-size: 1rem;
  font-weight: semi-bold;
}

ul li a p {
  font-family: "Montserrat", system-ui;
  font-optical-sizing: auto;
  font-size: 0.9rem;
  font-style: normal;
}

@media (min-width: 768px) {
  .contenedor-posts {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    padding-top: 2rem;
  }
}
.post {
  background-color: rgb(226.5, 226.5, 226.5);
  border: 1px solid rgb(202.5, 202.5, 202.5);
  margin-bottom: 2rem;
  width: 90%;
  margin: 0 auto;
}
.post .contenido-post {
  padding: 2rem;
}
.post h3, .post p {
  margin: 0;
}
.post a {
  background-color: rgba(155, 149, 149, 0.7176470588);
  color: #FFFFFF;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.post a:hover {
  background-color: rgba(142.6213592233, 135.8786407767, 135.8786407767, 0.7176470588);
  cursor: pointer;
}

.formulario {
  width: 60%;
  margin: 0 auto;
}
.formulario p {
  font-size: 1.4rem;
  color: rgba(155, 149, 149, 0.7176470588);
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: rgba(155, 149, 149, 0.7176470588);
}
.formulario label {
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}